import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image/withIEPolyfill"

import styles from "./hippo-meow-v2.module.scss"
import EmptyLine from "../../components/empty-line"
import hippoMeowV2Image from "../../images/hippoMeowV2.png"
import hippoMeowV2FontsImage from "../../images/hippoMeowV2Fonts.png"

export default () => {
  const { mockupHippoMeowV2 } = useStaticQuery(graphql`
    query {
      mockupHippoMeowV2: file(relativePath: { eq: "mockupHippoMeowV2.png" }) {
        childImageSharp {
          fixed(height: 100) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <React.Fragment>
      <div className={styles.pageHeadingSection}>
        <Img
          fixed={mockupHippoMeowV2.childImageSharp.fixed}
          alt=""
          className={styles.pageHeadingImage}
        />
      </div>
      <div className={styles.pageMainContentSection}>
        <div className={styles.pageMainContent}>
          <EmptyLine />
          <div>
            <div className={styles.projectTitle}>Hippomeow</div>
            <div className={styles.projectSubTitle}>
              Cake &amp; Cafe Shop Website
            </div>
          </div>
          <EmptyLine variant="Paragraph" />
          <div className={styles.projectText}>
            <div>
              A family cake and cafe shop website - nearly the same layout and
              content as the first design above. However, I turn it into a more
              classic and traditional interface.
            </div>
            <EmptyLine variant="Paragraph" />
            <div>
              The homepage imitates the interface of a shop in reality with wood
              walls, lean-to, neon signboard, and window. This brings a more
              traditional and cozier atmosphere for users.
            </div>
            <EmptyLine variant="Paragraph" />
            <div className={styles.projectDetailsRow}>
              <div className={styles.projectDetailsLabel}>Main color:</div>
              <div>
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.green}`}
                />
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.red}`}
                />
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.black}`}
                />
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.white}`}
                />
              </div>
            </div>
            <EmptyLine variant="Paragraph" />
            <div className={styles.projectDetailsRow}>
              <div className={styles.projectDetailsLabel}>Fonts:</div>
              <img src={hippoMeowV2FontsImage} alt="" />
            </div>
            <EmptyLine variant="Paragraph" />
            <div>Full page:</div>
            <EmptyLine variant="Paragraph" />
          </div>
        </div>
      </div>
      <div className={styles.projectImageContainer}>
        <img src={hippoMeowV2Image} className={styles.projectImage} alt="" />
      </div>
      <EmptyLine />
    </React.Fragment>
  )
}
